.wrapper {
  font-family: "Roboto";
  font-size: 14px;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  margin: 0 auto;
  width: 900px;
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.header {
  flex: 1 0 auto;
}